<template>
	<div class="warp">
		<!-- 功能按钮组 -->
		<h1 class="btn_group">
			<!-- 搜索 -->
			<!--<input type="button" value="搜索" @click="showSearch()" />-->
			<input type="text" placeholder="影视" @click="showSearch()" />
		</h1>
		
		<!--<h1 style="color: #42BD56;margin: 10px 0;">{{ msg }}</h1>-->
		
		<!-- 筛选按钮 -->
		<h1 class="movie_style">
			<input type="button" value="正在热映" :class="{curr_style: movieStyle == 'hot'}" @click="hotMovie()" />
			<input type="button" value="即将上映" :class="{curr_style: movieStyle == 'coming'}" @click="comingMovie()" />
			<input type="button" value="TOP 250" :class="{curr_style: movieStyle == 'top250'}" @click="movieTop250()" />
		</h1>
		
		<!-- 加载动画 -->
		<div class="loading" v-show="isLoading">
			<img src="../assets/loading.gif" />
		</div>
		
		<!-- 电影列表 -->
		<div>
			<ul class="movie_list">
				<li v-for="item in listFilm">
					<label class="thumb_label">
						<img :src="img403Settle(item.images.small)"/>
					</label>
					<p class="thumb_name">{{ item.title }}</p>
				</li>
			</ul>
		</div>
		
		<transition name="fade">
			<!-- 搜索容器 -->
			<div class="search_wrap" v-show="isShowSearch">
				<h1 class="btn_group" style="padding-right: 0;">
					<input type="text" placeholder="电影名 演员" v-model="keyword" />
					<input type="button" value="取消" @click="fadeSearch()" />
				</h1>
				
				<div class="search_res">
					<ul>
						<li v-for="item in searchResult">
							<label class="thumb_label">
								<img :src="img403Settle(item.images.small)" />
							</label>
							<p class="thumb_name">{{ item.title }}</p>
						</li>
					</ul>
				</div>
			</div>
		</transition>
		
	</div>
</template>



<script>
	import axios from "axios";
	
	export default {
		name: "douban",
		data() {
			return {
				msg: "豆瓣",
				//电影对象
				listFilm: "",
				//当前展示的电影类别
				movieStyle: "hot",
				//控制搜索容器显隐
				isShowSearch: false,
				//搜索关键字
				keyword: "",
				//搜索结果对象
				searchResult: "",
				//加载状态
				isLoading: true,
				//接口url统一管理
				apiList: 
				{
					inTheaters: process.env.API_HOST + "movie/in_theaters",
					comingSoon: process.env.API_HOST + "movie/coming_soon",
					top250: process.env.API_HOST + "movie/top250",
					search: process.env.API_HOST + "movie/search"
				}
			}
		},
		created()
		{
			//页面加载之后执行，默认展示正在热映的电影
			axios.get(this.apiList.inTheaters).then((_data) => {
				this.isLoading = false;
				this.listFilm = _data.data.subjects;
			});
			this.getData(this.apiList.inTheaters);
		},
		
		watch: 
		{
			//当搜索框的值发生变化时调用搜索方法
			"keyword": "searchMovie"
		},
		
		methods: 
		{
			//获取豆瓣电影数据
			getData(_url)
			{
				axios.get(_url).then((_data) => {
//					console.log(_data);
					this.listFilm = _data.data.subjects;
					this.isLoading = false;
				});
			},
			
			//正在热映
			hotMovie()
			{
				console.log("正在热映");
				this.isLoading = true;
				this.listFilm = "";
				this.getData(this.apiList.inTheaters);
				this.movieStyle = "hot";
			},
			
			//即将上映
			comingMovie()
			{
				console.log("即将上映");
				this.isLoading = true;
				this.listFilm = "";
				this.getData(this.apiList.comingSoon);
				this.movieStyle = "coming";
			},
			
			//电影排行top250
			movieTop250()
			{
				console.log("排行");
				this.isLoading = true;
				this.listFilm = "";
				this.movieStyle = "top250";
				this.getData(this.apiList.top250);
			},
			
			//显示搜索容器
			showSearch()
			{
				this.keyword = "";//清除上一次的搜索关键词
				this.searchResult = "";//清除上一次的搜索结果
				this.isShowSearch = true;
			},
			
			//隐藏搜索容器
			fadeSearch()
			{
				this.isShowSearch = false;
			},
			
			//搜索方法
			searchMovie()
			{
				axios.get(this.apiList.search,
				{
					params: 
					{
						q: this.keyword
					}
				}).then((_data) => 
				{
					console.log(_data);
					this.searchResult = _data.data.subjects;
				});
			},
			
			//当我们访问某些接口的时候，解决了接口跨域问题，然而又出现了图片403禁止访问问题
			//这种设计，是api厂商正常保证自己的服务器不被刷流量
			//图片403缓存处理
			img403Settle(_url)
			{
				//利用这个网站来处理图片api地址
				if(_url !== undefined)
				{
					return "https://images.weserv.nl/?url=" + _url.substring(7);
				}
			}
		}
	}
</script>



<style scoped>
	* {
		font-family: "微软雅黑";
		margin: 0;
		padding: 0;
		box-sizing: border-box;
	}
	
	h1,
	h2 {
		font-weight: normal;
	}
	
	ul {
		list-style-type: none;
		padding: 0;
	}
	
	li {
		display: inline-block;
		/*margin: 0 10px;*/
	}
	
	a {
		color: #42BD56;
	}
	
	.movie_style {
		display: flex;
		justify-content: space-between;
	}
	
	.movie_style input[type="button"] {
		flex-grow: 1;
		height: 36px;
		border: none;
		background: #fff;
		display: block;
	}
	
	.movie_style input[type="button"].curr_style {
		border-bottom: 4px solid #42BD56;
		color: #42BD56;
	}
	
	.movie_list {
		display: flex;
		overflow: auto;
	}
	
	.movie_list li {
		margin: 10px 14px 0 0;
	}
	
	.thumb_label {
		display: block;
		height: 161px;
		overflow: hidden;
	}
	
	.thumb_name {
		width: 115px;
		line-height: 48px;
		font-size: 12px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	
	.thumb_label img {
		width: 115px;
	}
	
	
	/* btn_group */
	.btn_group {
		background: #42BD56;
		display: flex;
		height: 56px;
		padding: 0 10px 10px;
		align-items: flex-end;
		justify-content: space-between;
	}
	
	.btn_group input[type="button"] {
		display: inline-block;
	    height: 26px;
	    line-height: 26px;
	    /*margin-left: 10px;*/
	    padding: 0 18px;
	    background-color: transparent;
	    color: #fff;
	    white-space: nowrap;
	    text-align: center;
	    font-size: 14px;
	    border: none;
	    cursor: pointer;
	}
	
	.btn_group input[type="text"] {
		flex-grow: 1;
		height: 30px;
		padding-left: 5px;
		border: none;
		border-radius: 4px;
	}
	
	/* search_wrap */
	.search_wrap {
		position: fixed;
		width: 100vw;
		height: 100vh;
		background: #fff;
		top: 0;
		left: 0;
	}
	
	.search_res {
		height: 100%;
		margin-top: 10px;
		overflow: auto;
	}
	
	.search_res ul {
		display: flex;
		flex-wrap: wrap;
		/*padding-left: 10px;*/
	}
	
	.search_res ul li {
		margin-left: 8px;
	}
	
	/* loading */
	.loading img {
		position: fixed;
		width: 100px;
		height: 100px;
		border-radius: 50%;
		top: calc(36% - 50px);
		left: calc(50% - 50px);
	}
	
	.fade-enter-active,
	.fade-leave-active {
		transition: opacity 0.3s;
	}
	
	.fade-enter,
	.fade-leave-to {
		opacity: 0;
	}
</style>


